<div id="login-page"><el-card shadow="always" style="max-width:400px"><template #header><div class="clearfix"><span>用户登录</span></div></template><el-form class="form-page" :model="form" ref="loginForm" :rules="rules" label-width="80px" :inline="false" size="normal"><el-form-item prop="username"><el-input v-model="form.username" prefix-icon="el-icon-user"></el-input></el-form-item><el-form-item prop="password"><el-input v-model="form.password" show-password prefix-icon="el-icon-lock"></el-input></el-form-item><el-form-item><el-button type="primary" @click="doLogin" style="width:100%">登录</el-button></el-form-item></el-form></el-card></div>