
import { defineComponent } from "vue"
import { ComponentElRef } from "/#/global"
import { reactive, ref } from "vue"
import { useStore } from "/@/store"
import { useRouter } from "vue-router"
import { ElMessage } from "element-plus"

export default defineComponent({
  setup() {
    const loginForm = ref<(ComponentElRef & { validate: (callback: (valid: boolean) => void) => void }) | null>(null)

    const form = reactive({
      username: "admin",
      password: "123456",
    })

    const rules = {
      username: [
        {
          required: true,
          message: "请输入用户名",
          trigger: "blur",
        },
      ],
      password: [
        {
          required: true,
          message: "请输入密码",
          trigger: ["blur"],
        },
      ],
    }

    const { state, dispatch } = useStore()

    const { push } = useRouter()

    const doLogin = () => {
      loginForm?.value?.validate((valid: boolean) => {
        if (valid) {
          dispatch("token/singIn", { username: form.username, password: form.password })
            .then(() => {
              push({ name: state.app.currentActiveMenu?.name ?? "Home" })
            })
            .catch((_err) => {
              ElMessage({
                message: "用户名或密码输入有误!",
                type: "error",
              })
            })
        }
      })
    }

    return {
      loginForm,
      form,
      rules,
      doLogin,
    }
  },
})
